import "../styles/progress.css";
import { Box } from "@mui/system";
import { Typography } from "@mui/material";
import { useLocation } from "react-router";

interface ProgressProps {}

const Progress: React.FC<ProgressProps> = () => {
  const { pathname } = useLocation();
  const information = pathname === "/information";
  // const cardInformation = pathname === "/verify-card";
  const uploadDocs = pathname === "/upload-docs";
  const success = pathname === "/account-secure";

  return (
    <Box
      sx={{
        width: "100%",
        padding: "3rem",
        paddingBottom: 0,
        display: success ? "none" : "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <Box
        sx={{
          width: ["100%", "60%"],
        }}
      >
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            width: "100%",
          }}
        >
          <Typography
            component="h3"
            sx={{
              fontSize: "1rem",
              fontWeight: "bold",
              marginBottom: "1rem",
            }}
          >
            Identification
          </Typography>
        </Box>
        <Box
          sx={{
            width: "100%",
            height: uploadDocs ? "8px" : "4px",
            background: uploadDocs ? "#0060f0" : "#949494",
            borderRadius: "4px",
            display: "flex",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              width: "33%",
              height: "8px",
              background: "#0060f0",
              borderRadius: "4px",
            }}
          />
          <Box
            sx={{
              width: "33%",
              height: information || uploadDocs ? "8px" : "4px",
              background: information || uploadDocs ? "#0060f0" : "#949494",
              borderRadius: "4px",
            }}
          />
        </Box>
      </Box>
    </Box>
  );
};

export default Progress;
